import { WorkSharp } from "@material-ui/icons";
import React, { Component } from "react";
import SimpleAccordion from "./Accordion";

class Resume2 extends Component {
  render() {
    if (this.props.data) {
      var education = this.props.data.education.map(function (education) {
        return (
          <div key={education.school} className="education">
            <div>
              {" "}
              <h3>{education.school}</h3>
              <p className="info">
                {education.degree} <span>&bull;</span>
                <em className="date">{education.graduated}</em>
              </p>
              <p>{education.description}</p>
            </div>

            <img src={education.logo} className="logo" alt="logo" />
          </div>
        );
      });
      var works = this.props.data.work.map(function (work) {
        return (
          <SimpleAccordion
            title={work.title}
            description={work.description}
            details={work.details}
            company={work.company}
            image={work.logo}
            years={work.years}
          />
        );
      });
    }

    return (
      <section id="resume">
        <div className="row education">
          <div className="three columns header-col">
            <h1>
              <span>Education</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">{education}</div>
            </div>
          </div>
        </div>

        <div className="row work">
          <div className="three columns header-col">
            <h1>
              <span>Work</span>
            </h1>
          </div>

          <div className="nine columns main-col">{works}</div>
        </div>
      </section>
    );
  }
}

export default Resume2;
